import React from 'react'
import $ from 'jquery'

class SlideUpButton extends React.Component{
    constructor(){
        super()
        this.state = {
            visible:false
        }
    }
    
    componentWillMount(){
        window.addEventListener('scroll',()=>{
            if(window.pageYOffset > 30){
                if (!this.state.visible){
                    this.setState({visible:true})
                }
            }
            else{
                if (this.state.visible){
                    this.setState({visible:false})
                }
            }
        })
    }
    
    componentWillUnmount(){
        window.removeEventListener('scroll')
    }
    
    slideUp(){
        $("html, body").animate({
            scrollTop: 0
        }, 800, function() {})
    }
    
    render(){
        return(
            <button className='slideUpButton' style={{opacity: this.state.visible ? '1' : '0'}} onClick={() => this.slideUp()}><i className="fas fa-arrow-up fa-2x"></i></button>
        )
    }
}

export default SlideUpButton