import React from 'react'

class ServiceBox extends React.Component{
    constructor(){
        super()
        
        
    }
    render(){
        return(
            <div className='servicesBox'>
                <img src={require( "" + this.props.src )} alt='imagealt'></img>
                <h2>{this.props.title}</h2>
                <div onClick={() => {this.props.setContent(this.props.id)}}>viac informácií</div>
            </div>
        )
    }
}

export default ServiceBox