import React from 'react'
import ServicesBox from './ServicesBox.jsx'
import ServicesContent from './ServicesContent.jsx'

//child of Slider

class ServiceScreen extends React.Component{
    constructor(){
        super()
        this.state = {currentContent:0}
        this.setContent = this.setContent.bind(this)
    }
    
    setContent(id){
        this.setState({currentContent:id})
    }
    
    render(){
        return(
            <div className='servicesScreen' style={{backgroundAttachment : this.props.swipeState ? 'scroll': 'fixed'}}>
                <div className='servicesCont'>
                    <ServicesBox setContent={this.setContent} id='1' src='./iconAI.png' title='AI Hub' />
                    <ServicesBox setContent={this.setContent} id='2' src='./iconData.png' title='DocuMind riešenia' />
                    <ServicesBox setContent={this.setContent} id='3' src='./iconAnalysis.png' title='Procesné a IT analýzy' />
                    <ServicesBox setContent={this.setContent} id='4' src='./iconSolutions.png' title='Vývoj na mieru' />
                </div>
                <ServicesContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
            </div>
        )
    }
}

export default ServiceScreen