import React from 'react'
import HomeScreen from '../home/HomeScreen.jsx'
import ServicesScreen from '../services/ServicesScreen.jsx'
import ContactScreen from '../contact/ContactScreen.jsx'
import ReferencesScreen from '../references/ReferencesScreen.jsx'



class Slider extends React.Component {
   
    constructor(){
        super()
    }
    render(){

        console.log(this.props.swipeState)
        const screens = [
            {
                src: <div className='blackScreen' />
            },
                        {
                src: <HomeScreen notifySlider={this.props.notifySlider}/>
            },
            {
               src: <ServicesScreen swipeState={this.props.swipeState}/>
            },
            {
               src: <div className='greenScreen' />
            },
            {
               src: <ReferencesScreen swipeState={this.props.swipeState}/>
            },
            {
               src: <ContactScreen />
            }
            
        ]
        
        switch (this.props.swipeState){
            case 0:
                return( <div style={{marginLeft: '-100%',transition:'none'}} className='sliderCont'>
                                    <div className='blankScreen' />
                                    {screens[this.props.currentScreen].src}
                                    <div className='blankScreen' />
                                </div>)
                break
            case 1:
                return( <div style={{marginLeft: '0%',transition:'all .5s'}} className='sliderCont'>
                                    {screens[this.props.currentScreen].src}
                                    {screens[this.props.previousScreen].src}
                                    <div className='blankScreen' />
                                </div>)
                break
            case 2:
                return( <div style={{marginLeft: '-200%',transition:'all .5s'}} className='sliderCont'>
                                    <div className='blankScreen' />
                                    {screens[this.props.previousScreen].src}
                                    {screens[this.props.currentScreen].src}
                                </div>)
                break
        }
        
    }
}

export default Slider