import React from 'react'
import MenuClick from './MenuClick.jsx'


const logo = require('./logo.png')
const menuData = [{
    id: 2,
    name:'Naše Služby'
},{
    id: 3,
    name:'Využitie'
},{
    id: 4,
    name:'Referencie'
},{
    id: 5,
    name:'Kontakt'
}]


class Menu extends React.Component{
    
    constructor(){
        super()
    }
    
    
    render(){
        const menuItems = menuData.map(item => {
            return(<MenuClick key={item.id} id={item.id} click={this.props.notifySlider} name={item.name} active={item.id == this.props.activeSlide}/>)
        })
        return(
            <nav id="navbar">
                    <input type="checkbox" id="check" ></input>
            <label htmlFor="check" className="checkbtn">
                <i className="fas fa-bars"></i>
            </label>
                <img src={logo} alt='MindIT logo' className='logo-mobile' />
            <ul className="menu">
                <a onClick={() => {this.props.notifySlider(1)}} style={{display:'inline'}}>
                    <img src={logo} alt='MindIT logo' className='logo'></img></a>
                {menuItems}
            </ul>
            </nav>
        )
    }
}

export default Menu