import React from 'react'
import ServicesContent from './components/services/ServicesContent.jsx'

const screens = [
    {src: <div></div>},
     {src: <ServicesContent />},
     {src: <div></div>},
     {src: <div></div>},
     {src: <div></div>}
    
]

class MainContent extends React.Component{
    render(){
        return(
            <div>
                {screens[this.props.currentScreen-1].src}
            </div>
        )
    }
}

export default MainContent