import React from 'react'


class MenuClick extends React.Component{
    constructor(){
        super()
        
    }
    
    render(){
        return(
        <a className="menuclick" onClick={() => {
            this.props.click(this.props.id)
         }}>
            <li className="menuitem " style={{color: this.props.active ? '#3487DD' : 'white', pointerEvents: this.props.active ? 'none' : 'auto'}} >{this.props.name}</li>
        </a>
    )
    }
}

export default MenuClick