import React from 'react'

class HomeScreen extends React.Component{
    render(){
        return(
            <div className='homeScreen'>
                <h1 className='mainHeading '>Robíme<br></br> skutočné<span className='AI'> AI</span> 
                </h1>
                <button onClick={() => this.props.notifySlider(2)} className='moreInfo slideHvr'>viac informácií</button>
            </div>
        )
    }
}

export default HomeScreen