import React from 'react'
import Slider from './components/slider/Slider.jsx'
import Menu from './components/menu/Menu.jsx'
import MainContent from './MainContent.jsx'
import SlideUpButton from './components/SlideUpButton.jsx'


class App extends React.Component{
    constructor(){
        super()
         this.state = {
            currentScreen : 1,
            previousScreen: 0,
            swipeState : 0
        }
        this.avoidClick = false
        this.notifySlider = this.notifySlider.bind(this)
    }
    
    notifySlider(slideId){
        if (window.slideID != slideId && !window.avoidClick){
            window.slideID = slideId
            this.setState({
                activeSlide : slideId
            })
            this.handleSwipe(slideId)
        }
    }
    
    handleSwipe(currentScreen){
        if (!this.avoidClick){
        window.avoidClick = true
        this.setState((prevState) => {
            if (currentScreen<this.state.currentScreen){
                return {
                       previousScreen: prevState.currentScreen,
                        swipeState:1,
                       currentScreen: currentScreen
                      }
            }
            else{
                return {
                       previousScreen: prevState.currentScreen,
                        swipeState:2,
                       currentScreen: currentScreen
                      }
            }
        })
        
        this.avoidClick = true
        setTimeout(() => {this.setState({swipeState:0})
                            this.avoidClick = false
                            window.avoidClick = false
                         }, 500);
        }
    }
    
    render(){
        return(
            <div>
                <Menu 
                    activeSlide={this.state.currentScreen}
                    notifySlider={this.notifySlider}/>
                <div className='header'>
                    <Slider currentScreen={this.state.currentScreen}
                    previousScreen={this.state.previousScreen}
                    swipeState={this.state.swipeState}  notifySlider={this.notifySlider}/>   
                </div>
                <SlideUpButton />
            </div>
        )
    }
}


export default App
    
