import React from 'react'
import $ from 'jquery'

class ServicesContent extends React.Component{
    
    constructor(){
        super()
    }
        
    
    componentDidUpdate(){
        if ($('.content').length>0 && this.props.swipeState == 0){
            $("#root").stop().animate({
                scrollTop: $('.content').position().top
            }, 800, function() {})
        }
    }
    
    render(){
        const screens = [
            {src:<div></div>},
            {src:<div className='content' style={{background:'red', height:'1000px'}}></div>},
            {src:<div className='content' style={{background:'blue', height:'1000px'}}></div>},
            {src:<div className='content' style={{background:'yellow', height:'1000px'}}></div>},
            {src:<div className='content' style={{background:'green', height:'1000px'}}></div>}
        ]
        return(
            <div className='servicesContent'>
                {screens[this.props.currentContent].src}
            </div>
        )
    }
}


    

export default ServicesContent