import React from 'react'

class ChooserButton extends React.Component{
    
    constructor() {
        super()
    }
    
    render(){
        return(
            <button style={{
                        backgroundColor: this.props.active ? '#3487DD' : '#9e9e9e',
                        pointerEvents: this.props.active ? 'none':'auto'}}
                    className='chooserButton' onClick={()=>{this.props.onclick(this.props.data.id)}}>{this.props.data.title}</button>
        )
    }
}

export default ChooserButton